import React,{useState} from "react"
import { useParams } from 'react-router';
import "../ImagenSeccion/image.css"
import styled from "styled-components"
import {Link} from "react-scroll"
import Carousel from 'react-elastic-carousel'
import {  Grid } from "@mui/material"
import Img from "react-cool-img";

const Image = styled.img`
height: auto;
width:100%;
object-fit:contain;


@media (min-width: 768px) {
    height:100vh;
}
@media (max-width: 767px) {
   margin-top:100px
}
`; 


 const   ImagenSeccionAuto = () => {
    const[value, setValue]=useState();
    const { itemIndex } = useParams();
    const handleChange = (e) =>{
        setValue(e.target.value)
    }



    
    return (
       <>
    
        <div class="containerDesktop" style={(itemIndex==="2"||"3")?{marginBottom:"100px"}:{marginBottom:"auto"}}>
       
        <Carousel itemsToShow={1} pagination={false} showArrows={false} enableSwipe={false}>

        
            {itemIndex==="1"?
                <Grid container spacing={2} className="container-grid-carousel">
                
                <Grid item xs={12} sm={6} md={6} lg={6}>
            <Image  src="/asegurapro/cotizador/assets/image/iconolandingpage-Automvil-01.svg" class="img-fluid"   alt="..." />
                </Grid>

                <Grid item xs={12} sm={6} md={6} lg={6}>
                    <item>
                    <h1> Rueda tranquilo en diciembre nosotros nos encargamos de cuidar tu carro! </h1>
                    <h2> Contrata un seguro para automviles con AseguraPro y Seguros Pirmide. Contamos con planes que se ajustan a tu medida y te garantizamos respuesta frente a cualquier emergencia, incluyendo robo y prdida total.  </h2>
    <item><Link to="auto" smooth={true} spy={true} offset={-70} exact={true} duration={1} activeClass="active">
            <Img onClick={handleChange} src="/asegurapro/cotizador/assets/image/Bot2n-02.jpg" class="img-button"   alt="..." /></Link></item>
                    </item> 
                </Grid>
            
                </Grid>
            :null}

            {itemIndex==="2"?
                <Grid container spacing={2} className="container-grid-carousel">
                
                <Grid item xs={12}  sm={6} md={6} lg={6}>
            <Image src="/asegurapro/cotizador/assets/image/iconolandingpage-Automvil-03.svg" class="img-fluid"   alt="..." />
                </Grid>

                <Grid item xs={12}  sm={6} md={6} lg={6}>
                    <item>
                    <h1>Si en diciembre vas a viajar en tu carro, es importante que cuentes con un seguro que te brinde respaldo kilmetro a kilmetro.</h1>
                    <h2> Imagina que decides tomar tu vehculo y viajar varios kilmetros
                        para encontrarte con ese familiar o amigo al que tienes meses sin ver y,
                        de la nada, te quedaste accidentado, sufriste un pequeo accidente o,
                        peor: te robaron el carro! No puedes evitar que estas cosas pasen,
                        pero s puedes estar prevenido al comprar un seguro para tu automvil
                        que te d respuestas al momento y proteja tu inversin al respaldar tu
                        vehculo, incluso, frente a siniestros parciales y prdida total.<br/> <br/>
                        Compra un seguro para automviles con AseguraPro y Seguros
                        Pirmide.     
                    </h2>
            
        <item><Link to="auto" smooth={true} spy={true} offset={-70} exact={true} duration={1} activeClass="active">
            <Img onClick={handleChange} src="/asegurapro/cotizador/assets/image/Bot2n-02.jpg" class="img-button" lazy cache   alt="..." /></Link></item>
                    </item> 
                </Grid>
            
                </Grid>
            :null}

            {itemIndex==="3"?
                <Grid container spacing={2} className="container-grid-carousel">
                
                <Grid item xs={12}  sm={6} md={6} lg={6}>
            <Img  src="/asegurapro/cotizador/assets/image/iconolandingpage-Automvil-05.svg" class="img-fluid" lazy cache   alt="..." />
                </Grid>

                <Grid item xs={12}  sm={6} md={6} lg={6} className="text-container-landing">
                    <item>
                    <h1>Estos son los beneficios que obtienes cuando contratas un seguro de RCV con gra mediante AseguraPro y Seguros Pirmide: </h1>
                    <ul>
                        <li>Comprando nuestra Pliza de RCV con gra cumples con un requisito obligatorio para circular en cualquier parte de Venezuela. 
                        </li>
                        <li>Cuentas con asistencia vial en cualquier parte del pas. 
                        </li>
                        <li>Puedes usar el servicio de gra cuantas veces lo necesites. 
                        </li>
                        
                    </ul>
                    <br/>
                    <h2>Compra un seguro para automviles con AseguraPro y Seguros Pirmide.</h2>
    <item><Link to="auto" smooth={true} spy={true} offset={-70} exact={true} duration={1} activeClass="active">
            <Img onClick={handleChange} src="/asegurapro/cotizador/assets/image/Bot2n-02.jpg" class="img-button"  lazy cache  alt="..." /></Link></item>
                    </item> 
                </Grid>
            
                </Grid>
            :null}
     
       
</Carousel>
       
       
 

</div> 



</>
    )
}
export default ImagenSeccionAuto;